/* ---------------------------------------------------------------------- */
/*  Reset
/* ---------------------------------------------------------------------- */
html, body{ font-size: 12px; height:100%;}
/* body{ background-color:#f1f1f2; font-family: 'roboto', 'Nanum Barun Gothic', sans-serif;} */
body{ font-size: 16px; background-color:#f2f3f7; font-family: 'roboto', sans-serif; }
ol,ul,li{ list-style:none; padding:0; margin:0; }
em{ font-style:normal; }
img{ max-width:100%; }
caption{ display:none; }
label{ position:relative; }
form, object{ border:0; padding:0; margin:0; display:block; }
object{ height:0;}
iframe{width: 1px; min-width: 100%; *width: 100%; height: 100%;}
a{ color:#555; }
a:hover,
a:focus{ color:#000; text-decoration:none; }
/* ,      */
a{
	-webkit-tap-highlight-color:rgba(0,0,0,0);
	-webkit-tap-highlight-color: transparent;
}
/*   , none     */
-webkit-appearance:none;
-webkit-appearance:text;
-webkit-appearance:radio;
-webkit-appearance:checkbox;
.clearfix:before, 
.clearfix:after {
	content: "";
	display: table;
}
.clearfix:after {
	clear: both;
}
.blind {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
}
/* ---------------------------------------------------------------------- */
/* checkbox, radio, input
/* ---------------------------------------------------------------------- */
label{
	position:relative;
	/* cursor: pointer; */
}
.radio, 
.checkbox{
    margin-bottom: 15px;
}
.radio label, 
.checkbox label{
	padding-left: 36px;
   /*  min-height: 30px; */
}
.radio + .radio, 
.checkbox + .checkbox{
	margin-top: 0px;
}
.radio input[type="radio"], 
.radio-inline input[type="radio"], 
.checkbox input[type="checkbox"], 
.checkbox-inline input[type="checkbox"]{
    display: inline-block;
    width: 24px;
    height: 24px;
    margin: 0;
    position: absolute;
    top: 0px;
    left: 0;
    background: url('/images/iconset_label.png') no-repeat;
    background-size: 150px 150px;
     background-position: -2px 0px;
    -webkit-appearance: none;
    outline: 0;
    border: 1px solid #ccd1d9;
    border-radius: 50%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.checkbox input[type="checkbox"]:checked,
.checkbox-inline input[type="checkbox"]:checked{
    background-position: -34px 0px;
    background-color: #283593;
    border: 0;
}
.chk-check input[type="checkbox"],
.chk-check input[type="checkbox"]:checked{
	border-color: transparent;
}
.chk-check input[type="checkbox"]:checked{
	background-position: -127px 0px;
	background-color: transparent;
}
.check-all .checkbox{
	text-align: center;
	margin-bottom: 0px;
}
.check-all .checkbox:checked{
	color: red;
}
