/* ------------------------------------------- */
.shake-effect{
	-webkit-animation: shake 0.4s linear 1 alternate;
	animation: shake 0.4s linear 1 alternate;
}
@-webkit-keyframes shake {
	0%, 100%{-webkit-transform:translateX(0px);transform:translateX(0px);}
	20%, 60%{-webkit-transform:translateX(8px);transform:translateX(8px);}
	40%, 80%{-webkit-transform:translateX(-8px);transform:translateX(-8px);}
}
@keyframes shake {
	0%, 100%{-webkit-transform:translateX(0px);transform:translateX(0px);}
	20%, 60%{-webkit-transform:translateX(8px);transform:translateX(8px);}
	40%, 80%{-webkit-transform:translateX(-8px);transform:translateX(-8px);}
}
