	transform: translateY(-50%);
}
.ui-switch input[type="checkbox"]{
	display: none;
}
.ui-switch input[type="checkbox"] + label:before{
	content: '';
	display: inline-block;
	vertical-align: middle;
	width: 62px;
	height: 38px;
	background: url('/images/ico_switch_off.png') no-repeat center;
	background-size: contain;
	-webkit-transition: all .1s ease;
	-webkit-transition: all .3s ease;
	transition: all .3s ease;
}
.ui-switch input[type="checkbox"]:checked + label:before{
	background: url('/images/ico_switch_on.png') no-repeat center;
	background-size: contain;
}
label[data-form-control=toggle] input[type=checkbox]:checked+[data-form-decorator=before]{
    background-color: #82b1ff;
    cursor: pointer;
}
label[data-form-control=toggle]{
    display: inline-block;
    position: relative;
    width: 50px;
    height: 25px;
    margin: 0;
    padding: 0;
    border-radius: 15px;
    background-color: #ddd;
    line-height: 16px;
    cursor: pointer;
    -webkit-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
}
label[data-form-control=toggle] input[type=checkbox]+[data-form-decorator=before]{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 15px;
    background-color: #ddd;
    -webkit-transition: background-color .35s;
    transition: background-color .35s;
    cursor: pointer;
}
label[data-form-control=toggle] input[type=checkbox]{
    margin-left: -9999px;
    cursor: pointer;
}
label[data-form-control=toggle] input[type=checkbox]:checked+[data-form-decorator=before] [data-form-decorator=after]{
    left: 50%;
    right: 0;
    cursor: pointer;
}
label[data-form-control=toggle] input[type=checkbox]+[data-form-decorator=before] [data-form-decorator=after]{
    position: absolute;
    top: 0;
    left: 0;
    right: 50%;
    bottom: 0;
    border-radius: 13px;
    margin: 2px;
    -webkit-box-shadow: 0 3px 5px rgba(0,0,0,.25);
            box-shadow: 0 3px 5px rgba(0,0,0,.25);
    background: #fff;
    -webkit-transition: all .35s;
    transition: all .35s;
    cursor: pointer;
}
label[data-form-control=toggle] input[type=checkbox]:checked+[data-form-decorator=before] [data-form-decorator=after]{
    left: 50%;
    right: 0;
    cursor: pointer;
}
